body {
  margin: 0;
  background-color: $light-grey;
  color: $black;
  line-height: 1.5;
  font-family: $primary-font-stack;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

p {
  margin-top: 0;
}
ul {
  padding: 0px;
  margin: 0px;
}
a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  padding: 12px;
}
form {
  text-align: left;
}
label {
  margin-bottom: 10px;
}
button {
  max-width: fit-content;
}

.container {
  /* margin-left: 5%;
  margin-right: 5%; */
  margin-top: 10px;
  margin-bottom: 10px;

  border-radius: 4px;
  padding: 20px;
  box-shadow: 7px 7px 13px 0 rgb(0 0 0 / 38%);
}
